$default-color: rgb(30, 30, 30);
$paused-color: rgb(100, 100, 100);
$active-color: rgb(51, 102, 255);

.timer {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;

    &__header {}

    &__time {
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 40px;
        font-weight: bold;
        height: 300px;
        width: 300px;
        color: $default-color;
        border-radius: 100%;
        background: linear-gradient(145deg, #ffffff, #e6e6e6);
        box-shadow: 15px 15px 30px #a8a8a8, -15px -15px 30px #ffffff;
        position: relative;
        transition: .3s ease-in-out color;

        &::after {
            content: '';
            position: absolute;
            height: 80%;
            width: 80%;
            background-color: transparent;
            border-radius: 50%;
            box-shadow: inset -3px -3px 7px #e6e6e6, inset 3px 3px 5px rgba(94, 104, 121, 0.671); }

        &_paused {
            color: $paused-color; } }

    &__progress-bar {
        height: 300px;
        width: 300px;

        circle {
            fill: transparent; }

        path {
            stroke: $active-color;
            transition: 1s ease-in-out all; } }

    &__controller {
        display: flex;
        align-items: center;
        justify-content: center; }

    &__button {
        cursor: pointer;
        margin: 30px;
        height: 75px;
        width: 75px;
        font-weight: bold;
        border-radius: 205px;
        background: linear-gradient(145deg, #ffffff, #e6e6e6);
        box-shadow: 8px 8px 15px #a8a8a8, -8px -8px 15px #ffffff;
        outline: none;
        border: none;
        user-select: none;
        transition: .3s ease-in-out box-shadow;

        &:hover {
            box-shadow: 4px 4px 8px #a8a8a8, -4px -4px 8px #ffffff; }

        &:focus {
            box-shadow: 2px 2px 4px #a8a8a8, -2px -2px 4px #ffffff; }

        &:disabled {
            cursor: initial;
            box-shadow: none;

            .icon {
                svg {
                    fill: $paused-color; } } } } }

